import React, { Component }  from 'react'
import { Link ,Redirect} from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import { Button,Alert, Card, CardImg,CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row, CardHeader } from 'reactstrap';
import CIcon from '@coreui/icons-react'
import logo from '../../../assets/ma.png'
import $ from 'jquery'
import conf from '../../../conf.js'
import ModalMessage from "../messages/ModalMessage"

  class ModifierPwd extends Component {
    constructor(props) {

      super(props)
    
      this.state = {
        ok:"",
        valider:false,
        show:false,
        success:true,
        boutton:"OK",
        message:"Mot de passe modifié avec succès !!",
        titre:"Modification du mot de passe",
  
      }
    }
    
  handleUpdatePassword=()=>{


    let  token=  JSON.parse(localStorage.getItem('token_auth'))
  

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify({
        "oldPassword": $("#mdp0").val(),
        "newPassword": $("#mdp2").val(),
        "idUser":token.user.id
      }
  )
  };
  if($("#mdp1").val()=== $("#mdp2").val()){

  fetch(conf.url+"user/updatePassword/user",requestOptions)
  .then((response) => response.json())
  .then((data) => {
     if(data===true){
      this.setState({show:true,success:true,valider:false});
     }else{
      $("#mdp0").val("")
      $("#mdp1").val("")
      $("#mdp2").val("")
      this.setState({show:true,success:false,valider:true});
     }
     
     

   }
   
  );
  }else{
    $("#mdp1").val("")
    $("#mdp2").val("")
    this.setState({show:true,success:false});
  }
    
  } 

  handleValidate=()=>{
    if(this.state.success){
      localStorage.removeItem('token_auth')
      this.setState({ok: <Redirect to='/login'/>});
    }else{
      this.setState({show:false})
    }
    
  }

  handleAuth=()=>{
    var acces=JSON.parse(localStorage.getItem('token_auth'))
    console.log("acces===>",acces)
    try{
      if(acces.role=="ADMIN"){
       this.setState({ok:<Redirect to='/dashboard'/>})
      }
      if(acces.role=="PROPRIETAIRE_TERRAIN"){
        this.setState({ok:<Redirect to='/liste-des-reservations'/>})
      }
      if(acces.role=="VISITEUR"){
          this.setState({ok:<Redirect to='/mes-reservations'/>})
      }
    }catch(error){this.setState({ok:<Redirect to='/login'/>})}
  }

 render (){

  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      {this.state.ok}
      <ModalMessage
        show={this.state.show}
        success={this.state.success}
        boutton={this.state.boutton}
        handleValidate={this.handleValidate}
        message={this.state.message}
        titre={this.state.titre}
        onCloseClick={() => this.setState({ show: false })}
      />
      <CContainer>
      <button onClick={this.handleAuth} className="btn btn-success mb-5" >Retour</button>
        <CRow className="justify-content-center">
        
          <Col md="6">
          
                <CardGroup>
  
                  <Card className="p-4 bg-white ">
                    
                    {/* <h3 ClassName="mb-3">Mot de passe oublié</h3> */}
                    <h5 className="p-4 text-center" >Modifier votre mot de passe</h5>
                    <CardBody>
                        
                      <Form ClassName="mt-3">
                        <Row>
                          
                          <InputGroup className="mb-3">
                            <Input type="password" id="mdp0" placeholder="Entrez votre mot de passe"  />
                          </InputGroup>

                          <InputGroup className="mb-3">
                            <Input type="password" id="mdp1" placeholder="Nouveau mot de passe"  />
                          </InputGroup>
                          
                          <InputGroup className="mb-4">
                            <Input type="password" id="mdp2" placeholder="Répetez le mot de passe"  />
                          </InputGroup>

                          <Col>
                            <Button onClick={this.handleUpdatePassword} color="success" className="px-4 text-white validation btn-block">Modifier votre mot de passe</Button>
                          </Col>

                        </Row>
                        {this.state.valider?
                        <Row>
                          <Alert color="danger">
                              Mot de passe incorrect!!!!.Veuillez fournir un mot de passe correcte.
                          </Alert>
                        </Row>:null
                        }
                      </Form>
                    </CardBody>
                  </Card>
                  
                </CardGroup>
              </Col>
        </CRow>
      </CContainer>
    </div>
  )
}
}
export default ModifierPwd
